<template>
  <x-header >
    单位换算
  </x-header>
  <div class="unit-show-box">
    <div class="unit-show-screen">
      <div class="unit-show-screen-left">
        <input v-model="currentLeftValue" @keyup="conversionOnLeft" number></br>{{currentLeftUnit.name}}
      </div>
      <div class="" style="width:40px;padding-top:40px;">
        <img src="../../assets/equal.png" style="margin-left:6px;" />
      </div>
      <div class="unit-show-screen-right">
        <input v-model="currentRightValue" @keyup="conversionOnRight" number></br>{{currentRightUnit.name}}
      </div>
    </div>
  </div>
  <div class="unit-show-list-box">
    <div class="unit-show-list-screen">
      <div class="unit-show-list">
        <div class="unit-selected left"></div>
        <ul class="unit-show-list-item" v-touch:panup="unitPanUpOnLeft | debounce 500" v-touch:pandown="unitPanDownOnLeft | debounce 100">
          <li v-for="unit in showedUnitsOnLeft" transition="unit">
            {{unit.name}}
          </li>
        </ul>
      </div>
      <div style="width:40px;">
        <div class="unit-selected">
          <img src="../../assets/topdownarrow.png" style="margin-top:22px;" />
        </div>
      </div>
      <div class="unit-show-list">
        <div class="unit-selected right"></div>
        <ul class="unit-show-list-item" v-touch:panup="unitPanUpOnRight | debounce 500" v-touch:pandown="unitPanDownOnRight | debounce 100">
          <li v-for="unit in showedUnitsOnRight" transition="unit">
            {{unit.name}}
          </li>
        </ul>
      </div>
    </div>
  </div>
  <footer>
    <div class="unit-tabbar">
      <div class="unit-tabbar-item" @click="showUnits(1)" :class="{ 'active': unitTab == 1 }">
        长度
      </div>
      <div class="unit-tabbar-item" @click="showUnits(2)" :class="{ 'active': unitTab == 2 }">
        面积
      </div>
      <div class="unit-tabbar-item" @click="showUnits(3)" :class="{ 'active': unitTab == 3 }">
        重量
      </div>
      <div class="unit-tabbar-item" @click="showUnits(4)" :class="{ 'active': unitTab == 4 }">
        体积
      </div>
      <div class="unit-tabbar-item" @click="showUnits(5)" :class="{ 'active': unitTab == 5 }">
        温度
      </div>
    </div>
  </footer>
</template>
<script>
  import XHeader from 'vux/dist/components/x-header'
  import Scroller from 'vux/dist/components/scroller'
  import formula from '../../components/formula/formula'

  export default {
    data () {
      return {
        env: process.env.NODE_ENV,
        leftOptions: {
          showBack: true,
          backText: 'Back',
          preventGoBack: true
        },
        currentLeftValue: 0.0,
        currentLeftUnit: { name: '米', value: 'm' },
        currentRightValue: 0.0,
        currentRightUnit: { name: '米', value: 'm' },
        unitTab: 1,
        showedUnitsOnLeft: [
          { name: '米', value: 'm' },
          { name: '分米', value: 'dcm' },
          { name: '厘米', value: 'cm' },
          { name: '毫米', value: 'mm' },
          { name: '千米', value: 'km' },
          { name: '公里', value: 'km' },
          { name: '里', value: 'li' },
          { name: '海里', value: 'marine' },
          { name: '英尺', value: 'foot' },
          { name: '英里', value: 'mile' },
          { name: '英寸', value: 'inch' },
          { name: '英寻', value: 'fathom' },
          { name: '码', value: 'yard' },
          { name: '杆', value: 'pole' }
        ],
        showedUnitsOnRight: [
          { name: '米', value: 'm' },
          { name: '分米', value: 'dcm' },
          { name: '厘米', value: 'cm' },
          { name: '毫米', value: 'mm' },
          { name: '千米', value: 'km' },
          { name: '公里', value: 'km' },
          { name: '里', value: 'li' },
          { name: '海里', value: 'marine' },
          { name: '英尺', value: 'foot' },
          { name: '英里', value: 'mile' },
          { name: '英寸', value: 'inch' },
          { name: '英寻', value: 'fathom' },
          { name: '码', value: 'yard' },
          { name: '杆', value: 'pole' }
        ],
        lengthUnits: [
          { name: '米', value: 'm' },
          { name: '分米', value: 'dcm' },
          { name: '厘米', value: 'cm' },
          { name: '毫米', value: 'mm' },
          { name: '千米', value: 'km' },
          { name: '公里', value: 'km' },
          { name: '里', value: 'li' },
          { name: '海里', value: 'marine' },
          { name: '英尺', value: 'foot' },
          { name: '英里', value: 'mile' },
          { name: '英寸', value: 'inch' },
          { name: '英寻', value: 'fathom' },
          { name: '码', value: 'yard' },
          { name: '杆', value: 'pole' }
        ],
        areaUnits: [
          { name: '平方米', value: 'sqm' },
          { name: '平方厘米', value: 'sqcm' },
          { name: '平方公里', value: 'sqkm' },
          { name: '平方英尺', value: 'sqfoot' },
          { name: '平方英寸', value: 'sqinch' },
          { name: '平方英里', value: 'sqmile' },
          { name: '亩', value: 'mou' },
          { name: '公顷', value: 'hectare' }
        ],
        weightUnits: [
          { name: '千克', value: 'kg' },
          { name: '克', value: 'g' },
          { name: '磅', value: 'pound' },
          { name: '盎司', value: 'ounce' },
          { name: '克拉', value: 'carat' },
          { name: '两', value: 'tael' },
          { name: '吨', value: 'ton' }
        ],
        volumeUnits: [
          { name: '升', value: 'litre' },
          { name: '立方米', value: 'cubicm' },
          { name: '立方厘米', value: 'cubiccm' },
          { name: '加仑', value: 'gallon' },
          { name: '品脱', value: 'pint' },
          { name: '夸脱', value: 'quart' }
        ],
        temperatureUnits: [
          { name: '摄氏度', value: 'centidegree' },
          { name: '华氏度', value: 'degreefahrenheit' }
        ]
      }
    },
    route: {
      data (transition) {
      }
    },
    computed: {
    },
    methods: {
      showUnits (tab) {
        this.unitTab = tab
        switch (this.unitTab) {
          case 1:
            this.showedUnitsOnLeft = [].concat(this.lengthUnits)
            this.showedUnitsOnRight = [].concat(this.lengthUnits)
            this.currentLeftUnit = this.showedUnitsOnLeft[0]
            this.currentRightUnit = this.showedUnitsOnRight[0]
            break
          case 2:
            this.showedUnitsOnLeft = [].concat(this.areaUnits)
            this.showedUnitsOnRight = [].concat(this.areaUnits)
            this.currentLeftUnit = this.showedUnitsOnLeft[0]
            this.currentRightUnit = this.showedUnitsOnRight[0]
            break
          case 3:
            this.showedUnitsOnLeft = [].concat(this.weightUnits)
            this.showedUnitsOnRight = [].concat(this.weightUnits)
            this.currentLeftUnit = this.showedUnitsOnLeft[0]
            this.currentRightUnit = this.showedUnitsOnRight[0]
            break
          case 4:
            this.showedUnitsOnLeft = [].concat(this.volumeUnits)
            this.showedUnitsOnRight = [].concat(this.volumeUnits)
            this.currentLeftUnit = this.showedUnitsOnLeft[0]
            this.currentRightUnit = this.showedUnitsOnRight[0]
            break
          case 5:
            this.showedUnitsOnLeft = [].concat(this.temperatureUnits)
            this.showedUnitsOnRight = [].concat(this.temperatureUnits)
            this.currentLeftUnit = this.showedUnitsOnLeft[0]
            this.currentRightUnit = this.showedUnitsOnRight[0]
            break
          default:
            this.showedUnitsOnLeft = [].concat(this.lengthUnits)
            this.showedUnitsOnRight = [].concat(this.lengthUnits)
            this.currentLeftUnit = this.showedUnitsOnLeft[0]
            this.currentRightUnit = this.showedUnitsOnRight[0]
            break
        }
        this.operateOnLeft()
      },
      unitPanUpOnLeft () {
        this.showedUnitsOnLeft.push(this.showedUnitsOnLeft.splice(0, 1)[0])
        this.currentLeftUnit = this.showedUnitsOnLeft[0]
        this.operateOnLeft()
      },
      unitPanDownOnLeft () {
        this.showedUnitsOnLeft.unshift(this.showedUnitsOnLeft.splice(this.showedUnitsOnLeft.length - 1, 1)[0])
        this.currentLeftUnit = this.showedUnitsOnLeft[0]
        this.operateOnLeft()
      },
      unitPanUpOnRight () {
        this.showedUnitsOnRight.push(this.showedUnitsOnRight.splice(0, 1)[0])
        this.currentRightUnit = this.showedUnitsOnRight[0]
        this.operateOnRight()
      },
      unitPanDownOnRight () {
        this.showedUnitsOnRight.unshift(this.showedUnitsOnRight.splice(this.showedUnitsOnRight.length - 1, 1)[0])
        this.currentRightUnit = this.showedUnitsOnRight[0]
        this.operateOnRight()
      },
      conversionOnLeft (e) {
        e.preventDefault()
        this.operateOnLeft()
      },
      conversionOnRight (e) {
        e.preventDefault()
        this.operateOnRight()
      },
      operateOnLeft () {
        if (this.unitTab === 5) {
          if (this.currentLeftUnit.value === this.currentRightUnit.value) {
            this.currentRightValue = this.currentLeftValue
          } else if (this.currentLeftUnit.value === 'centidegree') {
            this.currentRightValue = this.currentLeftValue * 1.8 + 32
          } else if (this.currentLeftUnit.value === 'degreefahrenheit') {
            this.currentRightValue = (this.currentLeftValue - 32) / 1.8
          }
        } else {
          this.currentRightValue = formula.operate(this.unitTab, this.currentLeftUnit.value, this.currentRightUnit.value, this.currentLeftValue)
        }
      },
      operateOnRight () {
        if (this.unitTab === 5) {
          if (this.currentLeftUnit.value === this.currentRightUnit.value) {
            this.currentLeftValue = this.currentRightValue
          } else if (this.currentRightUnit.value === 'centidegree') { // 摄氏度
            this.currentLeftValue = this.currentRightValue * 1.8 + 32
          } else if (this.currentRightUnit.value === 'degreefahrenheit') {
            this.currentLeftValue = (this.currentRightValue - 32) / 1.8
          }
        } else {
          this.currentLeftValue = formula.operate(this.unitTab, this.currentRightUnit.value, this.currentLeftUnit.value, this.currentRightValue)
        }
      }
    },
    components: {
      XHeader,
      Scroller
    }
  }
</script>



