function operate (tab, l, r, x) {
  var arr = []
  switch (tab) {
    case 1:
      arr = formula.lengthFormulas
      break
    case 2:
      arr = formula.areaFormulas
      break
    case 3:
      arr = formula.weightFormulas
      break
    case 4:
      arr = formula.volumeFormulas
      break
    case 5:
      arr = formula.lengthFormulas
      break
    default:
      arr = formula.lengthFormulas
      break
  }
  for (var i = 0; i < arr.length; i++) {
    if (arr[i].l === l && arr[i].r === r) {
      if (arr[i].o === '*') {
        return (x * arr[i].f).toFixed(2)
      } else if (arr[i].o === '/') {
        return (x / arr[i].f).toFixed(2)
      }
    }
  }
}

module.exports = {
  operate: operate
}



